import React, { useEffect, useState } from "react";
import axios from 'axios';
import { SHOP_PAGE, SERVER_API } from "config";
import { Link } from 'react-router-dom';
import { toast } from 'react-toastify';

import Title from 'components/Title';
import ProductsList from 'components/ProductsList';
import Categories from './Categories';
import Services from './Services';

import './style.scss';

export default function HomePage() {
    const client = axios.create({ baseURL: SERVER_API });

    const [products, setProducts] = useState([]);
    const [onload, setOnload] = useState(true);

    const catchError = e => {
        if (e.response && e.response.data) {
            toast.error(e.response.data.errors[0]);
        } else {
            toast.error('Kết nối máy chủ thất bại, vui lòng thử lại');
        }
    }

    const getData = () => {
        client.get('/products').then((rs) => {
            setOnload(false);
            setProducts(s => rs.data.data);
        }).catch(e => catchError(e));
    }
    useEffect(() => getData(), []);

    return (
        <main style={{ minHeight: 'calc(100vh - 43px - 264px)' }}>
            <section className='home-banner'>
                <div className='container'>
                    <div className="flex">
                        <div className="c1"><img src="/images/banner/demo-banner-default.png" /></div>
                        <div className="c2">
                            <div>
                                <div className="gap--20 sm"></div>
                                <p><i className="fa fa-phone pi"></i> Hotline: 0986635767</p>
                                <p><i className="fa fa-map-marker pi"></i> Phường Trần Hưng Đạo, Thành phố Thái Bình, Thái Bình</p>
                                <p><i className="fa fa-clock-o pi"></i>Luôn mở cửa</p>
                            </div>
                        </div>
                    </div>
                </div>
            </section>


            <section>
                <div className="container">
                    <Title
                        title='SẢN PHẨM'
                        subtitle='MUA NHANH - GIÁ TỐT'
                        align='center'
                    />
                    <div className='gap'></div>
                    <ProductsList items={products} onload={onload} column={4} preview={true} />
                </div>
            </section>

            <div className="gap--50"></div>
        </main>
    )
}