import React from "react";
import ReactDOM from 'react-dom';

export default function Popup(p) {
    return (
        <React.Fragment>
            {ReactDOM.createPortal(
                <div className='backdrop' onClick={p.close}></div>
                , document.getElementById('popup'))}
            {ReactDOM.createPortal(
                <div className='popup-container animated fadeIn'>
                    {p.children}
                    <span className="popup-close" onClick={p.close}>&times;</span>
                </div>
                , document.getElementById('popup'))}
        </React.Fragment>
    )
}