// ---- ĐÂY LÀ COMPONENT PHỤ THUỘC CỦA PRODUCTS LIST
// KHÔNG THỂ SỬ DỤNG TRỰC TIẾP

import { useState } from 'react';
import { useNavigate } from "react-router-dom";
import { DETAIL_PAGE, SERVER_ASSET } from 'config';
import PreviewDetail from './PreviewDetail';
import { useDispatch } from "react-redux";
import { addItem } from 'store/cart';
import { toast } from 'react-toastify';

export default function Product(props) {
    const [showPopup, setShowPopup] = useState(false);
    const navigate = useNavigate();
    const dispatch = useDispatch();

    const showProductDetail = () => {
        // Nếu kích thước màn hình lớn hơn 992px, hiển thị popup detail preview
        // Nếu kích thước màn hình nhỏ hơn 992px, sẽ chuyển hướng thẳng đến detail page
        // Đương nhiên điều kiện show detail preview vẫn là props.preview là true
        if (window.innerWidth > 992 && props.preview) {
            setShowPopup(s => !s);
        } else {
            navigate(DETAIL_PAGE + '/' + props.data._id + '/' + encodeURIComponent(props.data.name.replaceAll(" ", "-")));
        }
    }

    const addToCart = () => {
        dispatch(addItem({
            id: props.data._id,
            img: SERVER_ASSET + props.data.images[0],
            name: props.data.name,
            price: props.data.price,
            amount: 1
        }));
        toast.success('Đã thêm vào giỏ hàng!')
    }

    return (<>
        <div className='product'>
            <div className={`cover product-image ${props.onload ? 'skeleton' : ''}`}>
                {!props.onload && (
                    <img src={SERVER_ASSET + props.data.images[0]} alt={props.data.name} onClick={showProductDetail} />
                )}
            </div>
            <h2>{!props.onload ? props.data.name : 'Đang tải...'}</h2>
            <p>{!props.onload ? new Intl.NumberFormat("de-DE").format(props.data.price) + ' VNĐ' : '-'}</p>
            <div className='align--center'>{!props.onload && (
                <>
                    <button className='btn black btn--icon btn--sm pi--30' onClick={addToCart}>Thêm vào giỏ hàng</button>
                    <div className='gap--10'></div>
                </>
            )}</div>
        </div>
        {showPopup && (
            <PreviewDetail data={props.data} close={showProductDetail} />
        )}
    </>)
}