// ---- ĐÂY LÀ COMPONENT PHỤ THUỘC CỦA PRODUCTS LIST
// KHÔNG THỂ SỬ DỤNG TRỰC TIẾP

import { Link } from 'react-router-dom';
import { DETAIL_PAGE, SERVER_ASSET } from 'config';
import Popup from 'UI/Popup';

export default function PreviewDetail(props) {
    return (
        <Popup close={props.close}>
            <div className='product-detail flex flex--gap'>
                <div className='col--6'>
                    <div className='cover'>
                        <img src={SERVER_ASSET + props.data.images[0]} alt={props.data.name} />
                    </div>
                </div>
                <div className='col--6 pd'>
                    <h2>{props.data.name}</h2>
                    <p>{new Intl.NumberFormat("de-DE").format(props.data.price) + ' VNĐ'}</p>
                    <p>{props.data.title}</p>
                    <div className='gap--20'></div>
                    <div>
                        <Link className='btn black btn--icon pi--30' to={DETAIL_PAGE + '/' + props.data._id + '/' + encodeURIComponent(props.data.name.replaceAll(" ", "-"))}>
                            <i className='fa fa-shopping-cart'></i> Xem chi tiết sản phẩm
                        </Link>
                    </div>
                </div>
            </div>
        </Popup>
    )
}