// RENDER: TITLE
// Nhận vào:
//     -- props.title (Bắt buộc) : Tiêu đề
//     -- props.subtitle 
//     -- props.align : left or center or right
// Callback: Không

// CSS & RENDER
import './style.scss';
export default function Title(props) {
    return (
        <div className={`title ${props.align ? 'title--' + props.align : ''}`}>
            {props.subtitle && (
                <p>{props.subtitle}</p>
            )}
            <h2>{props.title}</h2>
        </div>
    )
}