// RENDER: LIST PRODUCTS
// Nhận vào:
//     -- props.items ( bắt buộc ): Dữ liệu các products sẽ hiển thị
//     -- props.onload : Trạng thái, nếu là true sẽ hiển thị skeleton
//     -- props.column : Số cột từ 1 đến 6
//     -- props.preview : Nếu là true hiển thị popup xem trước, nếu là false sẽ chuyển thẳng về detail page
// Callback: Không

import Product from './Product';

// CSS & RENDER
import './style.scss';
export default function ProductsList(props) {
    let c = 3;
    if (props.column >= 6) c = 2;
    if (props.column === 5) c = '20p';
    if (props.column < 5) c = 12 / props.column;

    return (
        <div className='flex flex--gap products'>
            {props.items.length > 0 && (
                props.items.map((item, i) => {
                    return (
                        <div className={`col-lg-${c} col-sm-12`} key={i}>
                            <Product onload={props.onload} data={item} preview={props.preview} />
                        </div>
                    )
                })
            )}
            {props.items.length === 0 && (
                <div className='col pd align--center'>Không có sản phẩm nào!</div>
            )}
        </div>
    )
}